import { defineComponent } from "vue";
import { IonToolbar } from "@ionic/vue";
import { useI18n } from 'vue-i18n';
export default defineComponent({
  name: "AccountPage",
  components: {
    IonToolbar
  },
  setup() {
    const {
      locale
    } = useI18n();
    const selectEN = () => {
      locale.value = 'en';
    };
    const selectTC = () => {
      locale.value = 'tc';
    };
    const selectSC = () => {
      locale.value = 'sc';
    };
    return {
      selectEN,
      selectTC,
      selectSC
    };
  }
});